@mixin desktop(){
	@media (min-width: 1367px){
		@content
	}
}

@mixin ipadPro2{
	@media (max-width: 1366px){
		@content
	}
}

@mixin ipadPro{
	@media (max-width: 1024px){
		@content
	}
}

@mixin ipad{
	@media (max-width: 768px){
		@content
	}
}

@mixin iphone8{
	@media (max-width: 414px){
		@content
	}
}

@mixin iphone5{
	@media (max-width: 320px){
		@content
	}
}

/*nav*/
.navbar li:nth-child(3) a{
	color: #563529;
	@include iphone8(){
		color: #eee;
	}
}
/*banner start*/
.bg{
	width: 100%;
	background-image: url('../image/bg/knowledge.png');
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: 0 -100px;
	margin-top: 95px;
	@include desktop(){
		margin-top: 110px;
	}
	@media (max-width: 823px){
		margin-top: 65px;
	}
	@include iphone8(){
		margin-top: 80px;
	}
}
.bg figure{
	width: 50%;
	display: block;
	margin: auto;
	@include desktop(){
		width: 60%;
	}
	@include iphone8(){
		width: 100%;
	}
}
.bg img{
	width: 100%;
	vertical-align: middle;
	margin: auto;
}
/*banner end*/

/*====================================*/

/*main start*/
.mainArea{
	width: 100%;
	padding: 50px;
	font-family: Microsoft JhengHei;
	background: url(../image/bg/bg_main.png);
	@media (max-width: 823px){
		padding: 0;
	}
}
.news{
	width: 100%;
	padding: 10px;
	font-family: Microsoft JhengHei;
	background-color: rgb(69,43,33);
	div{
		margin-left: 75px;
	}
	h3{
		padding: 0 10px;
		color: #DECDA4;
		font-size: 16px;

		font-weight: normal;
		display: inline-block;
		border-right: 1px solid #ddd;
	}
	marquee{
		vertical-align: middle;
    width: 300px;
		margin-left: 2px;
		padding: 0 5px;
		font-size: 16px;
		color: #fff;
	}
	@include desktop(){
		h3{
			font-size: 24px;
		}
		marquee{
			font-size: 24px;
		}
	}
	@include ipadPro(){
		div{
			margin-left: 12px;
		}
	}
	@media (max-width: 823px){
		h3{
			font-size: 14px;
		}
		marquee{
			font-size: 14px;
		}
	}
	@include ipad(){
		div{
			margin-left: 15px;
		}
		h3{
			font-size: 14px;
		}
		marquee{
			font-size: 14px;
		}
	}
	@include iphone8(){
		div{
			margin: 0;
		}
		marquee{
			width: 200px;
		}
	}
	@include iphone5(){
		h3{
			font-size: 12px;
		}
		marquee{
			width: 180px;
			font-size: 12px;
		}
	}
}
.mainBox{
	width: 95%;
	margin: auto;
	padding: 20px;
	p{
		font-size: 18px;
	}
	@include desktop(){
		p{
			font-size: 28px;
		}
	}
	@media (max-width: 823px){
		p{
			font-size: 16px;
		}
	}
	@include ipad(){
		width: 100%;
	}
	@media (max-width: 568px){
		p{
			font-size: 14px;
		}
	}
	@include iphone8(){
		p{
			font-size: 16px;
		}
	}
	@include iphone5(){
		p{
			font-size: 14px;
		}
	}
}
.mainBox .know{
	width: 189px;
	font-size: 28px;
	position: relative;
	display: block;
	background: url('../image/know.png') no-repeat left center;
	background-size: 40px;
	color: #452B21;
	padding: 5px 0 5px 60px;
	@media (max-width: 823px){
		font-size: 22px;
	}
	@media (max-width: 736px){
		font-size: 20px;
	}
	@include desktop(){
		width: 225px;
		font-size: 40px;
	}
	@media (max-width: 568px){
		font-size: 18px;
	}
	@include iphone8(){
		font-size: 20px;
	}
	@include iphone5(){
		font-size: 18px;
	}
}
.mainBox .know:after{
	content: '';
	position: absolute;
	left: 58px;
	bottom: 5px;
	width: 115px;
	border-bottom: 2px solid #784c3a;
	@media (max-width: 823px){
		width: 92px;
	}
	@media (max-width: 736px){
		width: 85px;
	}
	@media (max-width: 568px){
		width: 77px;
	}
	@include desktop(){
		width: 163px;
	}
	@include iphone8(){
		width: 85px;
	}
	@include iphone5(){
		width: 77px;
	}
}
.content{
	width: 80%;
	margin: auto;
	margin-top: 30px;
	padding: 10px;
	@include ipadPro(){
		width: 100%;
	}
	@include iphone8(){
		width: 100%;
		padding: 0;
	}
}
.content .contBox{
	padding: 20px 10px;
	@media (max-width: 823px){
		width: 80%;
		margin: auto;
	}
	@include iphone8(){
		width: 100%;
		padding: 20px 0;
		border-top: 1px solid #784c3a;
	}
}
.content .title{
	font-size: 22px;
	font-weight: normal;
	font-family: Microsoft JhengHei;
	@media (max-width: 823px){
		font-size: 20px;
	}
	@media (max-width: 736px){
		font-size: 18px;
	}
	@include desktop(){
		font-size: 36px;
	}
	@media (max-width: 568px){
		font-size: 16px;
	}
	@include iphone8(){
		font-size: 18px;
	}
	@include iphone5(){
		font-size: 16px;
	}
}
.content .item{
	padding: 20px 10px;
	display: flex;
	justify-content: flex-start;
	flex-direction: row;
	@include ipadPro(){
		flex-direction: column;
	}
	@include iphone8(){
		display: unset;		
	}
}
.content .item.even{
	flex-direction: row-reverse;
	@include ipadPro(){
		flex-direction: column;
	}
}
.item .know_pic{
	margin: 0 15px;
	height: 100%;
	flex-basis: 50%;
	flex-grow: 1;
}
.contBox:nth-of-type(1){
	border-top: 2px solid #784c3a;
	border-right: 2px solid #784c3a;
	@include iphone8(){
		border-top: none;
		border-right: none;
	}
}
.contBox:nth-of-type(2){
	border-top: 2px solid #784c3a;
	border-left: 2px solid #784c3a;
	@include iphone8(){
		border-left: none;
	}
}
.contBox:nth-of-type(3){
	border-top: 2px solid #784c3a;
	border-right: 2px solid #784c3a;
	@include iphone8(){
		border-right: none;
	}
}
.contBox:nth-of-type(4){
	border-top: 2px solid #784c3a;
	border-left: 2px solid #784c3a;
	@include iphone8(){
		border-left: none;
	}
}
.item figure img{
	width: 100%;
	vertical-align: middle;
}
.item .know_txt{
	margin: 0 10px;
	padding: 0 10px;
	flex-basis: 50%;
	flex-grow: 1;
	@include ipadPro(){
		margin: 20px 10px; 
	}
}
.item p{
	margin-top: 15px;
	font-size: 16px;
	text-align: justify;
	letter-spacing: 1.3px;
	@include desktop(){
		font-size: 24px;
	}
	@media (max-width: 568px){
		font-size: 14px;
	}
	@include iphone8(){
		font-size: 16px;
	}
	@include iphone5(){
		font-size: 14px;
	}
}
/*main end*/

/*=====================================*/


#gotop {
    position: fixed;
    right: 20px;
    bottom: 30px;
    font-size: 40px;
    color: rgb(87,38,0);
    cursor: pointer;
    z-index: 1000;
    transition: 1s;
   	display: none;
   	animation: rubberBand 1s linear;

   	@include desktop(){
		right: 40px;
	  bottom: 60px;
	  font-size: 60px;
	}
}

#gotop:hover{
	transition: 1s;
	transform: translateY(-10px);
}